
.left-delete{
    width:100%; 
    height:0;
    padding-bottom: 30%;
    background: #ccc;
    position:relative;
    z-index:2;
}
.move{
    position: relative;
    width: 100%;
    height:0;
    padding-bottom: 30%; 
    transition: all .5s ease-in-out;
}
.deleteIcon{
    width: 30%;
    height:0;
    padding-bottom: 30%;
    background: url(../../assets/svg/junk-box.svg) no-repeat center center #d9534f;
    text-align: center; 
    position: absolute;
    right:0;
    top:0;
}



