























/*过度动画*/
.slide-fade-enter-active {
  transition: all .5s ease-in-out;
}
.slide-fade-leave-active {
  transition: all .5s ease-in-out;
}
.slide-fade-enter, .slide-fade-leave-active {
  -webkit-transform: translateX(100px);
          transform: translateX(100px);
  opacity: 0;
}

 



