#login_register[data-v-5c558c26]{width: 100%; height: 100%; position: fixed; left: 0; top: 0; background: #3a3a3a;
}
#login_register .login-head[data-v-5c558c26]{width: 100%; height: 50px; position: fixed; left: 0; top: 0;
}
#login_register .login-head .back-btn[data-v-5c558c26]{display: block; width: 50px; height: 50px; background: url(../../assets/svg/back.svg) no-repeat center center;
	background-size: 20px;
}
#login_register .login-pic[data-v-5c558c26]{width: 100px; height: 100px; position: fixed; left: 50%; margin-left: -50px; top: 50px; background: url(../../assets/svg/logo.svg) no-repeat center center; background-size: 100%;
}

/*#login{margin-top: -110px;}
#register{margin-top: -130px;}*/
.form-box[data-v-5c558c26]{position: absolute; left: 15%; top: 200px;
}
.login-box[data-v-5c558c26]{width: 70%;
}
.login-box .form-list[data-v-5c558c26]{width: 100%; height: 40px; margin-bottom: 15px;
}
.login-box .form-list span[data-v-5c558c26]{display: block; width: 40px; height: 40px; background: #262a2d; float: left;
}
.login-box .form-list span.acount[data-v-5c558c26]{background: url(../../assets/svg/acount.svg) no-repeat center center #262a2d; background-size: 65%;
}
.login-box .form-list span.password[data-v-5c558c26]{background: url(../../assets/svg/password.svg) no-repeat center center #262a2d; background-size: 65%;
}
.login-box .form-list label[data-v-5c558c26]{display: block; margin: 0; width: calc(100% - 40px); height: 40px; background: #3a4149; float: left;
}
.login-box .form-list label input[data-v-5c558c26]{text-indent: 1em; border: none; outline: none; width: 100%; height: 100%; background: transparent; color: #cfd2d9; font-size: 14px; letter-spacing: 1px;
}
.login-box .form-list label input[data-v-5c558c26]::-webkit-input-placeholder { /* WebKit browsers */
	color: #cfd2d9;
}
.login-box .form-list label input[data-v-5c558c26]:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
	color: #cfd2d9;
}
.login-box .form-list label input[data-v-5c558c26]::-moz-placeholder { /* Mozilla Firefox 19+ */
	color: #cfd2d9;
}
.login-box .form-list label input[data-v-5c558c26]:-ms-input-placeholder { /* Internet Explorer 10+ */
	color: #cfd2d9;
}
.login-box .btn-box button[data-v-5c558c26]{width: 100%; height: 40px; background: #4ba1ea; border: 1px solid #1f3a57; color: #fff; outline: none; font-size: 16px; letter-spacing: 1px; text-shadow:1px 1px 2px #666; position: relative;
}
.login-box .btn-box button[data-v-5c558c26]:active{box-shadow: 0 0 3px #111; top: 1px;
}
.login-box .text-box p[data-v-5c558c26]{float: left; margin: 0; line-height: 40px; font-size: 14px; color: #979596;
}
.login-box .text-box a[data-v-5c558c26]{float: right; margin: 0; line-height: 40px; font-size: 14px; color: #f4f4f4;
}
.login-box .text-box a[data-v-5c558c26]:active,.login-box .text-box a[data-v-5c558c26]:hover{text-decoration: none;
}




