
#publish_box {
	padding: 60px 0 0 0;
}
#publish_box .title {
	padding: 10px 0;
}
#publish_box .title h2 {
	margin: 0;
	height: 40px;
	padding-left: 30px;
	line-height: 40px;
	float: left;
	font-size: 20px;
}
#publish_box .title h2.new-title {
	background: url(../../assets/svg/new.svg) no-repeat left center;
	background-size: 20px;
	color: #1296db;
}
#publish_box .title h2.hot-title {
	background: url(../../assets/svg/hot.svg) no-repeat left center;
	background-size: 20px;
	color: #d81e06;
}
#publish_box .title h2.top-title {
	background: url(../../assets/svg/top.svg) no-repeat left center;
	background-size: 20px;
	color: #abcd03;
}
#publish_box .title a {
	margin: 0;
	display: block;
	padding: 0 20px;
	height: 40px;
	border: 1px solid #abcd03;
	line-height: 40px;
	float: right;
	border-radius: 20px;
	font-size: 14px;
	color: #abcd03;
}
#publish_box .title a:hover {
	text-decoration: none;
}
#publish_box .title a:active {
	background: #abcd03;
	color: #fff;
}
.publish-list {
	margin-bottom: 3px;
	background: #fff;
	position: relative;
}
.publish-list a {
	display: block;
	box-shadow: 0 0 5px #ccc;
}
.publish-list a:hover {
	text-decoration: none;
}
.publish-list .publish-pic {
	width: 100%;
	padding-bottom: 130%;
	height: 0;
	overflow: hidden;
	position: relative;
}
.publish-list .publish-pic img {
	width: 100%;
}
.publish-list .publish-text {
	width: 100%;
	background: #fff;
}
.publish-list .publish-text p {
	padding: 10px;
	margin: 0;
	font-size: 16px;
	color: #111;
}
.publish-list .publish-text i {
	display: block;
	font-style: normal;
	padding: 0 0 10px 10px;
	margin: 0;
	font-size: 12px;
	color: #1296db;
}
.loading-height {
	padding-bottom: 50%;
}
.publish-list.chose .publish-pic .float-board {
	opacity: 1;
}
@media (max-width: 720px) {
#publish_box {
		padding: 20px 0 0 0;
}
#publish_box .title h2 {
		font-size: 14px;
		height: 24px;
		line-height: 24px;
}
#publish_box .title a {
		font-size: 12px;
		height: 24px;
		padding: 0 10px;
		line-height: 24px;
}
.publish-list .publish-text p {
		font-size: 12px;
		padding: 5px;
}
.publish-list .publish-text i {
		padding: 0 0 5px 5px;
}
} 



